import {details} from "../../apis"
export default {
	data() {
		return {
			details:{}
		};
	},
	onLoad(option){
		uni.showLoading({
		    title: '加载中'
		});
		details(option.id).then(res=>{
			let [err,data]=res;
			this.details=data.data;
			uni.hideLoading();
		})
	}
}
